.item {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border-bottom: 1px solid #e0e0e0;
}

.img {
    display: block;
    max-width: 100%;
    height: auto;
    border-radius: 50%;
    margin-right: 25px;
}

.name {
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 10px;
}

.number {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    opacity: 0.5;
}

.button {
    width: 30px;
    height: 30px;
    margin-left: auto;
    border-radius: 50%;
    border: none;
    cursor: pointer;
    font-weight: 400;
}

.button:hover {
    background-color: rgba(154, 173, 190, 0.795);
    box-shadow: -1px 5px 8px 0px rgba(34, 60, 80, 0.2);
}